import React, { Component } from 'react';
import DressingRoom from './DressingRoom';

class SleevePage extends React.Component {
     constructor(props) {
          super(props);
          this.state = {
               Selected: ""
          };
     }
     
     SleeveSelect(Sleeve) {        
        this.setState({Selected: Sleeve});     
     }
     
     Continue() {
          if (this.state.selected != "") {
               this.props.SetValue("Sleeve", this.state.Selected);
               this.props.SetCurrent(this.props.Next);
          }
     }
     
     render() {
          
          var SleeveData = {
               Married: {
                    key: "Long",
                    tooltip: "Long sleeves are long",
                    proper: "Long"
               },
               Unmarried: {
                    key: "Short",
                    tooltip: "Short sleeves are short",                    
                    proper: "Short"
               }
          };
          
          var SleeveDataKeys = Object.keys(SleeveData);
          var Sleeves = [];
          
          for (var i = 0; i < SleeveDataKeys.length; i++) {
               var SleeveName = SleeveDataKeys[i];
               Sleeves.push(<Sleeve key={SleeveName} Data={SleeveData[SleeveName]} SleeveSelected={this.state.Selected} SleeveSelect={this.SleeveSelect.bind(this)} />);
          }
          
          var Stage = this.state.Selected != "" ? "Material" : "Sleeve"; 

          return (
               <div>
                    <h1>Select Sleeve Length</h1>
                    <p>Sleeve length affects how much of your arms are covered</p>
                    <div>
                         {Sleeves}
                         <DressingRoom ScenarioValues={this.props.ScenarioValues} Stage={Stage} />
                    </div>
                    <button onClick={this.Continue.bind(this)}>Next</button>
               </div>
          );
     }
}

class Sleeve extends React.Component {
     
     render() {
          var SleeveClass = "selectable";          
          SleeveClass += this.props.SleeveSelected == this.props.Data.key ? " selected" : "";
          
          return (
               <div className={SleeveClass} onClick={() => this.props.SleeveSelect(this.props.Data.key)}>
                    <p>{this.props.Data.proper}</p>
                    <a><i className="fa fa-info-circle fa-2"></i></a>          
               </div>          
          );
     }
}

export default SleevePage;


