import React, { Component } from 'react';

class ScenarioNav extends React.Component {
     constructor(props) {
          super(props);
     }
     
     SetPage(Page) {
          this.props.SetCurrent(Page);
     }
     
     render() {
          
          var MenuItems = Object.keys(this.props.Menu);

          var FormatMenu = {
               Season: "Season",
               Formality: "Formality",
               Marital: "Marital Status",
               Sleeve: "Sleeve Length",
               Material: "Material",
               Obi: "Obi",
               Pattern: "Pattern"               
          };
          
          var MenuLi = [];
          
          for (var i = 0; i < MenuItems.length; i++) {
               var Item = MenuItems[i];
               
               var ClassName = "menuItem " + this.props.Menu[Item].Class;
               
               MenuLi.push(
                    (<ScenarioListItem key={Item} ClassName={ClassName} SetPage={this.SetPage.bind(this)} PageName={Item} MenuText={FormatMenu[MenuItems[i]]} />)
               );
          }

          return (
               <ul>{MenuLi}</ul>
          );
     }
     
}

class ScenarioListItem extends React.Component {
      constructor(props) {
            super(props);
      }

      render () {

            return (
                  <li onClick={() => this.props.SetPage(this.props.PageName)}>
                        {this.props.MenuText}
                  </li>
            );
      }

}

export default ScenarioNav;


