                    MapleLeaf: this.state.Path + "maple_leaf_spring_tsukesage_" + this.state.Material.toLowerCase() + ".png",
                    Peony: this.state.Path + "peony_spring_tsukesage_" + this.state.Material.toLowerCase() + ".png",
