import React, { Component } from 'react';

class TitlePage extends React.Component {
    constructor(props) {
        super(props);
    }
          
    render() {
        return (
            <div>
                <h1>Kimono Play</h1>
                <button onClick={this.props.Restart}>Start</button>
            </div>
        );
    }
}

export default TitlePage;


