import React, { Component } from 'react';
import DressingRoom from './DressingRoom';
import AlertContainer from 'react-alert';

class ObiPage extends React.Component {
     constructor(props) {
          super(props);

          this.state = {
               Selected: "",
               ChosenImage: "/Images/Obi/Default.png",
               Season: this.props.ScenarioValues.Season.Value,
               Path: "/Images/Obi/",
               IconPath: "/Images/General/"
          };
     }
     
     ObiSelect(Obi, Image) {         
          this.setState({Selected: Obi, ChosenImage: Image});
          this.props.SetImage("Obi", Image);
     }
     
     Continue() {
          if (this.state.Selected != "") {
               this.props.SetValue("Obi", this.state.Selected, this.state.ChosenImage);
               this.props.SetCurrent(this.props.Next);
          }
     }
     
     render() {

        var Obis = {
            Summer: this.state.Path + "summer_obi.png",
            Spring: this.state.Path + "spring_obi.png",
            Autumn: this.state.Path + "fall_obi.png",
            Winter: this.state.Path + "winter_obi.png"
        }

        var ObiIcons = {
            SummerIcon: this.state.IconPath + "summer.png",
            SpringIcon: this.state.IconPath + "spring.png",
            AutumnIcon: this.state.IconPath + "fall.png",
            WinterIcon: this.state.IconPath + "winter.png"
        }
        
        var Seasons = Object.keys(Obis);

        var ObiSelection = [];

        for (var i = 0; i < Seasons.length; i++) {

            var AllowedObi = Seasons[i] == this.state.Season;
            var ImageLink = Obis[Seasons[i]];
            var IconLink = ObiIcons[Seasons[i] + "Icon"];
            var ObiName = Seasons[i];

            ObiSelection.push(
                <Obi key={i} AllowedObi={AllowedObi} ImageLink={ImageLink} IconLink={IconLink} ObiName={ObiName} ObiSelect={this.ObiSelect.bind(this)} />
            );
        }

        var Stage = this.state.Selected != "" ? "Pattern" : "Obi"; 
        
        return (
            <div>
                <h1>Select an Obi</h1>
                <div>
                    {ObiSelection}
                    <DressingRoom ScenarioValues={this.props.ScenarioValues} Stage={Stage} />
                </div>
                <button onClick={this.Continue.bind(this)}>Next</button>
            </div>
        );
     }
}

class Obi extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            DefaultImage: "/Images/Obi/Default.png"
        }
    }

    TriggerSeasonAlert = () => {
        this.msg.error('That pattern is not used in ' + this.props.Season, {
            time: 2000,
            type: 'error',
            icon: <img src="../../Images/General/error.png" />
        });
        this.props.ObiSelect("", this.state.DefaultImage);
    }

    Update() {
        if (!this.props.AllowedObi) {
            //this.TriggerSeasonAlert();
        } else {
            this.props.ObiSelect(this.props.ObiName, this.props.ImageLink);
        }
    }

    alertOptions = {
        offset: 14,
        position: 'bottom left',
        theme: 'dark',
        time: 5000,
        transition: 'scale' 
    }
    
    render () {
        return (
            <div onClick={this.Update.bind(this)}>
                <p>{this.props.ObiName}</p>
                <img width="50px" src={this.props.IconLink} />
            </div>
        );
    }
}

export default ObiPage;


