import React, { Component } from 'react';

class DressingRoom extends React.Component {
    constructor(props) {
        super(props);

        var ToDraw = [];
    
        if (this.props.Stage == "Sleeve" || this.props.Stage == "Material") {
            ToDraw.push("Juban");
        }

        if (this.props.Stage == "Obi") {
            ToDraw.push("Material");
        }

        if (this.props.Stage == "Pattern") {
            ToDraw.push("Material");
            ToDraw.push("Obi");
        }

        if (this.props.Stage == "Final") {
            ToDraw.push("Material");
            ToDraw.push("Obi");
            ToDraw.push("Pattern");
        }

        ToDraw.push("Mannequin");

        this.state = {
            Renderables: ToDraw,
            Stage: this.props.Stage
        }
    }

    componentDidMount() {

        const canvas = this.refs.canvas;
        const ctx = canvas.getContext("2d");
        const Mannequin = this.refs.Mannequin;
        const Juban = this.refs.Juban;
        const Material = this.refs.Material;
        const Obi = this.refs.Obi;
        const Pattern = this.refs.Pattern;
        
        Mannequin.onload = () => {
            ctx.drawImage(Mannequin, 0, 0);
        }

        Juban.onload = () => {
            if (this.state.Renderables.indexOf("Juban") > -1) {
                ctx.drawImage(Juban, 0, 0);
            }
        }

        Material.onload = () => {
            if (this.state.Renderables.indexOf("Material") > -1) {
                ctx.drawImage(Material, 0, 0);
            }
        }

        Obi.onload = () => {
            if (this.state.Renderables.indexOf("Obi") > -1) {
                ctx.drawImage(Obi, 0, 0);
            }
        }

        Pattern.onload = () => {
            if (this.state.Renderables.indexOf("Pattern") > -1) {
                ctx.drawImage(Pattern, 0, 0);
            }
        }
        
    }

    componentDidUpdate() {
        var ToDraw = [];
    
        if (this.props.Stage == "Sleeve" || this.props.Stage == "Material") {
            ToDraw.push("Juban");
        }

        if (this.props.Stage == "Obi") {
            ToDraw.push("Material");
        }

        if (this.props.Stage == "Pattern") {
            ToDraw.push("Material");
            ToDraw.push("Obi");
        }

        if (this.props.Stage == "Final") {
            ToDraw.push("Material");
            ToDraw.push("Obi");
            ToDraw.push("Pattern");
        }  

        const canvas = this.refs.canvas;
        const ctx = canvas.getContext("2d");
        const Mannequin = this.refs.Mannequin;
        const Juban = this.refs.Juban;
        const Material = this.refs.Material;
        const Obi = this.refs.Obi;
        const Pattern = this.refs.Pattern;

        Mannequin.onload = () => {
            ctx.drawImage(Mannequin, 0, 0);
        }

        Juban.onload = () => {
            if (ToDraw.indexOf("Juban") > -1) {
                ctx.drawImage(Juban, 0, 0);
            }
        }

        Material.onload = () => {
            if (ToDraw.indexOf("Material") > -1) {
                ctx.drawImage(Material, 0, 0);
            }
        }

        Obi.onload = () => {
            if (ToDraw.indexOf("Obi") > -1) {
                ctx.drawImage(Obi, 0, 0);
            }
        }

        Pattern.onload = () => {
            if (ToDraw.indexOf("Pattern") > -1) {
                ctx.drawImage(Pattern, 0, 0);
            }
        }
    }
    
    render() {
            
        return (
            <div>
                <canvas id="canvas" ref="canvas" width="657" height="850" />
                <img ref="Mannequin" src="/Images/Mannequin/mannequin.png" className="hidden" />
                <img ref="Juban" src="/Images/Mannequin/juban.png" className="hidden"  />
                <img ref="Material" src={this.props.ScenarioValues.Material.Image} className="hidden"  />   
                <img ref="Obi" src={this.props.ScenarioValues.Obi.Image} className="hidden"  /> 
                <img ref="Pattern" src={this.props.ScenarioValues.Pattern.Image} className="hidden"  />             
            </div>
        );
    }
}

export default DressingRoom;


