import React, { Component } from 'react';

class SeasonPage extends React.Component {
     constructor(props) {
          super(props);
          this.state = {
               Selected: ""
          };
     }
     
     SeasonSelect(Season) {
          this.setState({Selected: Season});
     }
     
     Continue() {
          if (this.state.selected != "") {
               this.props.SetValue("Season", this.state.Selected);
               this.props.SetCurrent(this.props.Next);
          }
     }
     
     render() {
          
          var SeasonData = {
               Spring: {
                    key: "Spring",
                    img: "http://i.imgur.com/sw99Geo.png",
                    proper: "Spring"
               },
               Summer: {
                    key: "Summer",                
                    img: "http://i.imgur.com/Y9ZS8lU.png",
                    proper: "Summer"
               },
               Autumn: {
                    key: "Autumn",                 
                    img: "http://i.imgur.com/pqnYvXx.png",
                    proper: "Autumn"
               },
               Winter: {
                    key: "Winter",               
                    img: "http://i.imgur.com/3M04cSH.png",
                    proper: "Winter"
               }  
          };
          
          var SeasonDataKeys = Object.keys(SeasonData);
          var Seasons = [];
          
          for (var i = 0; i < SeasonDataKeys.length; i++) {
               var SeasonName = SeasonDataKeys[i];
               Seasons.push(<Season key={SeasonName} Data={SeasonData[SeasonName]} SeasonSelected={this.state.Selected} SeasonSelect={this.SeasonSelect.bind(this)} />);
          }
          
          return (
               <div>
                    <h1>Select a Season</h1>
                    <p>Seasons affect the colour and design patterns of kimonos</p>
                    <div>
                         {Seasons}
                    </div>
                    <button onClick={this.Continue.bind(this)}>Next</button>
               </div>
          );
     }
}

class Season extends React.Component {
          
     render() {
          var SeasonClass = "selectable";
          SeasonClass += this.props.SeasonSelected == this.props.Data.key ? " selected" : "";
          
          return (
               <div className={SeasonClass} onClick={() => this.props.SeasonSelect(this.props.Data.key)}>
                    <p>{this.props.Data.proper}</p>
                    <a><i className="fa fa-info-circle fa-2"></i></a>
               </div>          
          );
     }
}

export default SeasonPage;


