import React, { Component } from 'react';

class MaritalPage extends React.Component {
     constructor(props) {
          super(props);
          this.state = {
               Selected: ""
          };
     }
     
     MaritalSelect(Marital) {        
        this.setState({Selected: Marital});     
     }
     
     Continue() {
          if (this.state.selected != "") {
               this.props.SetValue("Marital", this.state.Selected);
               this.props.SetCurrent(this.props.Next);
          }
     }
     
     render() {
          
          var MaritalData = {
               Married: {
                    key: "Married",
                    tooltip: "Marriage is when you are married",
                    proper: "Married"
               },
               Unmarried: {
                    key: "Unmarried",
                    tooltip: "Why you not married yet?!",                    
                    proper: "Unmarried"
               }
          };
          
          var MaritalDataKeys = Object.keys(MaritalData);
          var Maritals = [];
          
          for (var i = 0; i < MaritalDataKeys.length; i++) {
               var MaritalName = MaritalDataKeys[i];
               Maritals.push(<Marital key={MaritalName} Data={MaritalData[MaritalName]} MaritalSelected={this.state.Selected} MaritalSelect={this.MaritalSelect.bind(this)} />);
          }
          
          return (
               <div>
                    <h1>Select a Marital Status</h1>
                    <p>Marital status affects something or other</p>
                    <div>
                         {Maritals}
                    </div>
                    <button onClick={this.Continue.bind(this)}>Next</button>
               </div>
          );
     }
}

class Marital extends React.Component {
     
     render() {
          var MaritalClass = "selectable";          
          MaritalClass += this.props.MaritalSelected == this.props.Data.key ? " selected" : "";
          
          return (
               <div className={MaritalClass} onClick={() => this.props.MaritalSelect(this.props.Data.key)}>
                    <p>{this.props.Data.proper}</p>
                    <a><i className="fa fa-info-circle fa-2"></i></a>          
               </div>          
          );
     }
}

export default MaritalPage;


