import React, { Component } from 'react';

//Components
import TitlePage from './Components/Meta/TitlePage';
import FinalPage from './Components/Meta/FinalPage';

import ScenarioNav from './Components/ScenarioBuilder/ScenarioNav';
import SeasonPage from './Components/ScenarioBuilder/SeasonPage';
import MaritalPage from './Components/ScenarioBuilder/MaritalPage';
import FormalityPage from './Components/ScenarioBuilder/FormalityPage';
import SleevePage from './Components/DressingRoom/SleevePage';
import MaterialPage from './Components/DressingRoom/MaterialPage';
import PatternPage from './Components/DressingRoom/PatternPage';
import ObiPage from './Components/DressingRoom/ObiPage';

//Stylesheets
import './KimonoPlayApp.css';

class KimonoPlayApp extends Component {

  constructor(props) {
    super(props);

    this.state = {

      Current: "Title", //Current = Current screen visible
      ScenarioValues: { //Tracks the nav-style and value selected
        Season: {
          Value: "", //Selected value, "" if no selected value
          Class: "unlocked" //Used to determine how the navigation styles the link
        },
        Formality: {
          Value: "",
          Class: "locked"
        },
        Marital: {
          Value: "",
          Class: "locked"
        },
        Sleeve: {
          Value: "",
          Class: "locked",
          Image: ""
        }, 
        Material: {
          Value: "",
          Class: "locked",
          Image: ""
        },
        Obi: {
          Value: "",
          Class: "locked",
          Image: ""
        },
        Pattern: {
          Value: "",
          Class: "locked",
          Image: ""
        }
      },
      Next: { //The next page in relation to possible current pages
        Title: "Season",
        Season: "Formality",
        Formality: "Marital",
        Marital: "Sleeve",
        Sleeve: "Material",
        Material: "Obi",
        Obi: "Pattern",
        Pattern: "Final"
      }, 
      Complete: false
    }

  }

  //Set current state, used to navigate to an unlocked page (i.e. redo a choice)
  SetCurrent(Page) {

    if (Page != "Final" && this.state.ScenarioValues[Page].Class !== "locked") {
      var ScenarioValues = this.state.ScenarioValues;
      var MenuItems = Object.keys(ScenarioValues);
      var PageIndex = MenuItems.indexOf(Page);

      for (var i = 0; i < MenuItems.length; i++) {
        if (i < PageIndex) {
          ScenarioValues[MenuItems[i]].Class = "previous"; //For pages that have selected values
        } else if (i == PageIndex) { //Set page to this
          ScenarioValues[MenuItems[i]].Class = "current";
          ScenarioValues[MenuItems[i]].Value = "";
        } else {
          ScenarioValues[MenuItems[i]].Class = "locked";
          ScenarioValues[MenuItems[i]].Value = "";
        }
      }

      this.setState({ScenarioValues: ScenarioValues, Current: Page});
    } else {
      this.setState({Current: Page});
    }
  }

  Restart() {
    this.SetCurrent("Season");
    this.setState({Complete: false});
  }

  SetValue(Page, Value, Image="") { //Set a pages value and proceed to next page
    var ScenarioValues = this.state.ScenarioValues;
    ScenarioValues[Page].Value = Value;
    ScenarioValues[Page].Image = Image;

    if (Page == "Pattern") {
      this.setState({ScenarioValues: ScenarioValues, Complete: true});
    } else {
      var MenuItems = Object.keys(ScenarioValues);
      var NextPageIndex = MenuItems.indexOf(Page) + 1;
      var NextMenuItem = MenuItems[NextPageIndex];
      ScenarioValues[NextMenuItem].Class = "current";

      this.setState({ScenarioValues: ScenarioValues});
    }
  }

  SetImage(Page, Image="") {
    var ScenarioValues = this.state.ScenarioValues;

    //if (ScenarioValues[Page].Image != Image) {
    //  var Canvas = document.getElementById("canvas");
    //  var Context = Canvas.getContext("2d");
    //  Context.clearRect(0, 0, Canvas.width, Canvas.height);
    //}
    ScenarioValues[Page].Image = Image;

    this.setState({ScenarioValues: ScenarioValues});
  }

  render() {
    var ComponentsToRender = [];

    //Title Page
    var TitleComp = (<TitlePage Restart={this.Restart.bind(this)} Next={this.state.Next.Title} />);

    //Scenario Page Components
    var ScenarioNavigation = (<ScenarioNav Menu={this.state.ScenarioValues} SetCurrent={this.SetCurrent.bind(this)} />);
    var SeasonComp = (<SeasonPage SetValue={this.SetValue.bind(this)} SetCurrent={this.SetCurrent.bind(this)} Next={this.state.Next.Season} />);
    var FormalityComp = (<FormalityPage SetValue={this.SetValue.bind(this)} SetCurrent={this.SetCurrent.bind(this)} Next={this.state.Next.Formality} />);
    var MaritalComp = (<MaritalPage SetValue={this.SetValue.bind(this)} SetCurrent={this.SetCurrent.bind(this)} Next={this.state.Next.Marital} />);

    //Dressing Room Components
    var SleeveComp = (<SleevePage SetValue={this.SetValue.bind(this)} SetCurrent={this.SetCurrent.bind(this)} Next={this.state.Next.Sleeve} ScenarioValues={this.state.ScenarioValues} />);
    var MaterialComp = (<MaterialPage SetImage={this.SetImage.bind(this)} SetValue={this.SetValue.bind(this)} SetCurrent={this.SetCurrent.bind(this)} Next={this.state.Next.Material} ScenarioValues={this.state.ScenarioValues} />);
    var ObiComp = (<ObiPage SetImage={this.SetImage.bind(this)} SetValue={this.SetValue.bind(this)} SetCurrent={this.SetCurrent.bind(this)} Next={this.state.Next.Obi} ScenarioValues={this.state.ScenarioValues} />);
    var PatternComp = (<PatternPage SetImage={this.SetImage.bind(this)} SetValue={this.SetValue.bind(this)} SetCurrent={this.SetCurrent.bind(this)} Next={this.state.Next.Pattern} ScenarioValues={this.state.ScenarioValues} />);
    var FinalComp = (<FinalPage ScenarioValues={this.state.ScenarioValues} restart={this.Restart.bind(this)}/>);

    var ComponentsDirectory = {
      Title: [TitleComp],
      Season: [ScenarioNavigation, SeasonComp],
      Formality: [ScenarioNavigation, FormalityComp],
      Marital: [ScenarioNavigation, MaritalComp],
      Sleeve: [ScenarioNavigation, SleeveComp],
      Material: [ScenarioNavigation, MaterialComp],
      Obi: [ScenarioNavigation, ObiComp],
      Pattern: [ScenarioNavigation, PatternComp],
      Final: [FinalComp]
    };

    for (var i = 0; i < ComponentsDirectory[this.state.Current].length; i++) {
      ComponentsToRender.push(ComponentsDirectory[this.state.Current][i]);
    }

    return (
      ComponentsToRender
    );
  }
}

export default KimonoPlayApp;



