import React, { Component } from 'react';

class FormalityPage extends React.Component {
     constructor(props) {
          super(props);
          this.state = {
               Selected: ""
          };
     }
     
     FormalitySelect(Formality) {
        this.setState({Selected: Formality});
     }
     
     Continue() {
          if (this.state.Selected != "") {
               this.props.SetValue("Formality", this.state.Selected);
               this.props.SetCurrent(this.props.Next);
          }
     }
     
     render() {
          
          var FormalityData = {
               SemiFormal: {
                    key: "SemiFormal",                   
                    proper: "Semi-formal"
               },
               Formal: {
                    key: "Formal",                    
                    proper: "Formal"
               }
          };
          
          var FormalityDataKeys = ['SemiFormal', 'Formal'];
          var Formalities = [];
          
          for (var i = 0; i < FormalityDataKeys.length; i++) {
               var FormalityName = FormalityDataKeys[i];
               Formalities.push(<Formality key={FormalityName} Data={FormalityData[FormalityName]} FormalitySelected={this.state.Selected} FormalitySelect={this.FormalitySelect.bind(this)} />);
          }
          
          return (
               <div>
                    <h1>Select a Formality</h1>
                    <p>Formalities affect the colour and design patterns of kimonos</p>
                    <div>
                         {Formalities}
                    </div>
                    <button onClick={this.Continue.bind(this)}>Next</button>
               </div>
          );
     }
}

class Formality extends React.Component {
     
     render() {
          var FormalityClass = "";
          FormalityClass += this.props.FormalitySelected == this.props.Data.key ? " selected" : "";

          return (
               <div className={FormalityClass} onClick={() => this.props.FormalitySelect(this.props.Data.key)}>
                    <p>{this.props.Data.proper}</p>
                    <a><i className="fa fa-info-circle fa-2"></i></a>
               </div>          
          );
     }
}

export default FormalityPage;


