import React, { Component } from 'react';
import DressingRoom from './DressingRoom';
import AlertContainer from 'react-alert';

class MaterialPage extends React.Component {
     constructor(props) {
          super(props);
          this.state = {
               Selected: "",
               ChosenImage: "/Images/Kimonos/Default.png",
               Season: this.props.ScenarioValues.Season.Value,
               SleeveLength: this.props.ScenarioValues.Sleeve.Value,
               Path: "/Images/Kimonos/",
               IconPath: "/Images/General/"
          };
     }
     
     MaterialSelect(Material, Image) {         
          this.setState({Selected: Material, ChosenImage: Image});
          this.props.SetImage("Material", Image);
     }
     
     Continue() {
          if (this.state.Selected != "") {
               this.props.SetValue("Material", this.state.Selected, this.state.ChosenImage);
               this.props.SetCurrent(this.props.Next);
          }
     }
     
     render() {
        var MaterialData = {
            Long: {
                Awase: {
                    Autumn: "fall_long-sleeve_awase.png",
                    Spring: "spring_long-sleeve_awase.png",
                    Winter: "winter_long-sleeve_awase.png"
                },
                Hitoe: {
                    Autumn: "fall_long-sleeve_hitoe.png",
                    Summer: "summer_short-sleeve_hitoe.png"
                },
                Usumono: {
                    Summer: "summer_long-sleeve_usumono.png"
                }
            },
            Short: {
                Awase: {
                    Autumn: "fall_short-sleeve_awase.png",
                    Spring: "spring_short-sleeve_awase.png",
                    Winter: "winter_short-sleeve_awase.png"                   
                },
                Hitoe: {
                    Autumn: "fall_short-sleeve_hitoe.png"
                },
                Usumono: {
                    Summer: "summer_short-sleeve_usumono.png"
                }
            }
        };

        var AllowableMaterials = {
            Summer: {
                Long: ["Hitoe", "Usumono"],
                Short: ["Usumono"]
            }, 
            Spring: {
                Long: ["Awase"],
                Short: ["Awase"]
            },
            Autumn: {
                Long: ["Awase", "Hitoe"],
                Short: ["Awase", "Hitoe"]
            },
            Winter: {
                Long: ["Awase"],
                Short: ["Awase"]
            }
        }

        var MaterialsToShow = Object.keys(MaterialData[this.state.SleeveLength]);
        var KimonosToShow = MaterialData[this.state.SleeveLength];
        var AllowedMaterials = AllowableMaterials[this.state.Season][this.state.SleeveLength];
        var Kimonos = [];
        
        for (var i = 0; i < MaterialsToShow.length; i++) {
            var MaterialName = MaterialsToShow[i];
            var AllowedMaterial = AllowedMaterials.indexOf(MaterialName) > -1;

            var SeasonsToShow = KimonosToShow[MaterialName];
            var SeasonsToShowNames = Object.keys(SeasonsToShow);
            
            for (var j = 0; j < SeasonsToShowNames.length; j++) {
                var SeasonName = SeasonsToShowNames[j];
                var AllowedSeason = SeasonName == this.state.Season;
                var ImageLink = this.state.Path + KimonosToShow[MaterialName][SeasonName];
                var IconLink = this.state.IconPath + KimonosToShow[MaterialName][SeasonName];

                Kimonos.push(<Kimono key={i+""+j} IconLink={IconLink} AllowedMaterial={AllowedMaterial} Material={MaterialName} Season={this.state.Season} AllowedSeason={AllowedSeason} MaterialSelect={this.MaterialSelect.bind(this)} ImageLink={ImageLink} />);
            }
        }

        var Stage = this.state.Selected != "" ? "Obi" : "Material"; 

        return (
            <div>
                <h1>Select a Kimono Material and Colour</h1>
                <div>
                    {Kimonos}
                    <DressingRoom ScenarioValues={this.props.ScenarioValues} Stage={Stage} />
                </div>
                <button onClick={this.Continue.bind(this)}>Next</button>
            </div>
        );
     }
}

class Kimono extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            DefaultImage: "Images/Kimonos/Default.png"
        }
    }
    TriggerMaterialAlert = () => {
        this.msg.error('That material is not used in ' + this.props.Season, {
            time: 2000,
            type: 'error',
            icon: <img src="error.png" />
        }); //UI Prompt for not allowed material
        this.props.MaterialSelect("", this.state.DefaultImage);
    }

    TriggerSeasonAlert = () => {
        this.msg.error('That colour is not used in ' + this.props.Season, {
            time: 2000,
            type: 'error',
            icon: <img src="../../Images/General/error.png" />
        });
        this.props.MaterialSelect("", this.state.DefaultImage);
    }

    Update() {
        if (!this.props.AllowedMaterial) {
            //this.TriggerMaterialAlert();
        } else if (!this.props.AllowedSeason) {
            //this.TriggerSeasonAlert();
        } else {
            this.props.MaterialSelect(this.props.Material, this.props.ImageLink);
        }
    }

    alertOptions = {
        offset: 14,
        position: 'bottom left',
        theme: 'dark',
        time: 5000,
        transition: 'scale' 
    }
    
    render () {
        return (
            <div onClick={this.Update.bind(this)}>
                <p>{this.props.Material}</p>
                <img width="100px" src={this.props.IconLink} />
            </div>
        );
    }
}

export default MaterialPage;


